import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController, ToastController, AlertController, ActionSheetController, Platform } from 'ionic-angular';


import { Observable } from 'rxjs/Observable';
import { NotesService } from '../../services/notes.service';
import { Camera, CameraOptions } from '@ionic-native/camera';
import { AngularFireStorageModule, AngularFireStorage, AngularFireUploadTask } from 'angularfire2/storage';
import * as firebase from 'firebase';
import { InAppBrowser, InAppBrowserOptions } from '@ionic-native/in-app-browser';
/**
 * Generated class for the ModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-modal',
  templateUrl: 'modal.html',
})
export class ModalPage {

  counter: any;
  event: any;
  note: any = {
     
      id: null,
      anillo: null,
      nombre: null,
      palomar: null,
      color: null,
      padre: null,
      madre: null,
      vuelos:null,
      premios:null,
      observaciones:null,
      modificado:null
      
    
  };
  id : any = null;
  show=true;
  fotodx: any;
  cargado=false;
cargadoekg=false;
cargadorx=false;
preview=false;
previewekg=false;
previewrx=false;
someTextUrl ='';
someTextUrlekg ='';
someTextUrlrx ='';
iaboptions: InAppBrowserOptions = {
  location: 'no',
  zoom: 'yes'
  

};




  constructor(public navCtrl: NavController, public navParams: NavParams, public viewCtrl : ViewController,  
    public notesService : NotesService, public toastCtrl:ToastController, public alertCtrl:AlertController, public actionSheetCtrl: ActionSheetController,
    public platform: Platform, private camera: Camera, private afStorage: AngularFireStorage, private iab:InAppBrowser) {

      this.id = navParams.get('id');
      if (this.id != 0){
      notesService.getNote(this.id)
      .valueChanges().subscribe(note=> {
        
        this.note = note});

        this.cargado=true;
    this.cargadoekg=true;
    this.cargadorx=true 
      };

      this.getSomeText(); 


  }

  getSomeText() {

    firebase.storage().ref('pacientes/'+this.id+'/').child('tac').getDownloadURL()
      .then(response => {this.someTextUrl = response; 
                         this.preview=true } )
      .catch(error => console.log('error', error)
      
      

    );
    

      firebase.storage().ref('pacientes/'+this.id+'/').child('ekg').getDownloadURL()
      .then(response => {this.someTextUrlekg = response;
                         this.previewekg=true})
      .catch(error => console.log('error', error));

      firebase.storage().ref('pacientes/'+this.id+'/').child('rx').getDownloadURL()
      .then(response => {this.someTextUrlrx = response;
                        this.previewrx=true})
      .catch(error => console.log('error', error));

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ModalPage');
  }

  dismiss()
  {

    this.viewCtrl.dismiss();
  }

  capturartac() {
     
    const options: CameraOptions = {
      quality: 100,
      destinationType: this.camera.DestinationType.DATA_URL,
      encodingType: this.camera.EncodingType.JPEG,
      mediaType: this.camera.MediaType.PICTURE,
      saveToPhotoAlbum:true,
      allowEdit: true
      
    }
    this.camera.getPicture(options).then((imageData) => {
      // imageData is either a base64 encoded string or a file URI
      // If it's base64:
      let base64Image = 'data:image/jpeg;base64,' + imageData;
      const pictures= this.afStorage.storage.ref('pacientes/'+this.id+'/tac/' );
      pictures.putString(base64Image, 'data_url');
      
      let toast = this.toastCtrl.create({
        message: 'Pigeon guardado',
        duration: 2000,
        position: 'middle'
      });
      toast.present();
      
     
      

     }
    , (err) => {
      // Handle error
      console.log(err);
      let toast = this.toastCtrl.create({
        message: 'No se pudo tomar la foto',
        duration: 2000,
        position: 'middle'
      });
      toast.present();
     });
    
  }

  capturarekg(){
    const options: CameraOptions = {
      quality: 100,
      destinationType: this.camera.DestinationType.DATA_URL,
      encodingType: this.camera.EncodingType.JPEG,
      mediaType: this.camera.MediaType.PICTURE,
      saveToPhotoAlbum:true,
      allowEdit: true
    }
    this.camera.getPicture(options).then((imageData) => {
      // imageData is either a base64 encoded string or a file URI
      // If it's base64:
      let base64Image = 'data:image/jpeg;base64,' + imageData;
      const pictures= this.afStorage.storage.ref('pacientes/'+this.id+'/ekg/' );
      pictures.putString(base64Image, 'data_url');
      let toast = this.toastCtrl.create({
        message: 'Foto Guardada',
        duration: 2000,
        position: 'middle'
      });
      toast.present();

     }
    , (err) => {
      // Handle error
      console.log(err);
      let toast = this.toastCtrl.create({
        message: 'No se pudo tomar la foto',
        duration: 2000,
        position: 'middle'
      });
      toast.present();
     });
    

  }

  capturarrx(){
    const options: CameraOptions = {
      quality: 100,
      destinationType: this.camera.DestinationType.DATA_URL,
      encodingType: this.camera.EncodingType.JPEG,
      mediaType: this.camera.MediaType.PICTURE,
      saveToPhotoAlbum:true,
      allowEdit: true
    }
    this.camera.getPicture(options).then((imageData) => {
      // imageData is either a base64 encoded string or a file URI
      // If it's base64:
      let base64Image = 'data:image/jpeg;base64,' + imageData;
      const pictures= this.afStorage.storage.ref('pacientes/'+this.id+'/rx/' );
      pictures.putString(base64Image, 'data_url');
      let toast = this.toastCtrl.create({
        message: 'Foto guardada',
        duration: 2000,
        position: 'middle'
      });
      toast.present();

     }
    , (err) => {
      // Handle error
      console.log(err);
      let toast = this.toastCtrl.create({
        message: 'No se pudo tomar la foto',
        duration: 2000,
        position: 'middle'
      });
      toast.present();
     });
    

  }

  descargartac(){

    let target = "_self";

    this.iab.create(this.someTextUrl,target,this.iaboptions);

  }

  descargarekg(){
    let target = "_self";
    this.iab.create(this.someTextUrlekg,target,this.iaboptions);


  }

  descargarrx(){
    let target = "_self";
    this.iab.create(this.someTextUrlrx,target,this.iaboptions);


  }
  


  
 
 

  opcionespac(){
    if (this.id != 0){
    let actionSheet = this.actionSheetCtrl.create({
      title: 'Opciones',
      cssClass: 'action-sheets-basic-page',
      buttons: [
        
        {
          text: 'Guardar',
          icon: !this.platform.is('ios') ? 'md-add-circle' : null,
          handler: () => {
            console.log('Share clicked');
              if (this.id != 0){
                this.notesService.editarpac (this.note);
                let toast = this.toastCtrl.create({
                  message: 'Entrada modificada',
                  duration: 2000,
                  position: 'top'
                });
                toast.present();
               
              }
              else{
          
              this.note.id = Date.now ();
              this.notesService.crearpac (this.note);
              let toast = this.toastCtrl.create({
                message: 'Pigeon agregado',
                duration: 2000,
                position: 'top'
              });
              toast.present();
               }
              this.viewCtrl.dismiss();
              
            
          }
        },
        {
          text: 'Borrar Pigeon',
          role: 'destructive',
          icon: !this.platform.is('ios') ? 'trash' : null,
          handler: () => {
            console.log('Delete clicked');
            let alert = this.alertCtrl.create({
              title: 'Atención',
              subTitle: 'Se borrará el Pigeon de manera permanente',
              buttons: [
               
                {
                  text: 'Aceptar',
                  handler: () => {
                    this.show =false
                    this.notesService.borrarpac(this.note);
                    this.viewCtrl.dismiss();
                    let toast = this.toastCtrl.create({
                      message: 'Pigeon borrado',
                      duration: 2000,
                      position: 'top'
                    });
                    toast.present();
                    
                  }
                },
                {
                  text: 'Cancelar',
                  handler: () => {
                      this.viewCtrl.dismiss();
                  }
                },
        
        
               ]
            });
            alert.present();
         
          }
        },
        
        {
          text: 'Cancelar',
          role: 'cancel', // will always sort to be on the bottom
          icon: !this.platform.is('ios') ? 'close' : null,
          handler: () => {
            console.log('Cancel clicked');
            this.viewCtrl.dismiss();
          }
        }
      ]
    });
    actionSheet.present();
 }
else {
  let actionSheet = this.actionSheetCtrl.create({
    title: 'Opciones',
    cssClass: 'action-sheets-basic-page',
    buttons: [
      
      {
        text: 'Guardar',
        icon: !this.platform.is('ios') ? 'md-add-circle' : null,
        handler: () => {
          console.log('Share clicked');
            if (this.id != 0){
              this.notesService.editarpac (this.note);
              let toast = this.toastCtrl.create({
                message: 'Pigeon modificado',
                duration: 2000,
                position: 'top'
              });
              toast.present();
             
            }
            else{
        
            this.note.id = Date.now ();
            this.notesService.crearpac (this.note);
            let toast = this.toastCtrl.create({
              message: 'Pigeon agregado',
              duration: 2000,
              position: 'top'
            });
            toast.present();
             }
            this.viewCtrl.dismiss();
            
          
        }
      },
      
      
      {
        text: 'Cancelar',
        role: 'cancel', // will always sort to be on the bottom
        icon: !this.platform.is('ios') ? 'close' : null,
        handler: () => {
          console.log('Cancel clicked');
          this.viewCtrl.dismiss();
        }
      }
    ]
  });
  actionSheet.present();



}
}





}



