

import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController,ModalController, NavParams, ViewController, ToastController, AlertController, ActionSheetController, Platform } from 'ionic-angular';


import { Observable } from 'rxjs/Observable';
import { CruzasService } from '../../services/cruzas.service';
import { Modal4Page } from '../modal4/modal4'

import { Calendar } from '@ionic-native/calendar';

import { AngularFireStorageModule, AngularFireStorage, AngularFireUploadTask } from 'angularfire2/storage';


/**
 * Generated class for the Modal3Page page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-modal3',
  templateUrl: 'modal3.html',
})
export class Modal3Page {

  counter: any;
  event: any;
  cruzas: any = {
     
      id: null,
      idpaloma1: null,
      nombrepaloma1: null,
      anillopaloma1:null,
      
      idpaloma2: null,
      nombrepaloma2: null,
      anillopaloma2:null,
      

      fechacruza: null,
      fechatermino: null,
      aviso: null,

      
      observaciones:null,
      modificado:null
      
    
  };
  
  pigeona: any= {
    nombre: null,
    anillo:null,
    idpigeon:null
  };
  pigeonb: any ={
    nombre:null,
    anillo: null,
    idpigeon:null
  };

  id : any = null;
  show=true;

  eventoCal = { 
    title: "Appigeon ", 
    location: "", 
    message: "Los pigeons nacen mañana! Ya tenemos todo listo?", 
    };

datetest: Date;




  constructor(public navCtrl: NavController, public navParams: NavParams,  public viewCtrl : ViewController,  
    public cruzasService : CruzasService, public toastCtrl:ToastController, public alertCtrl:AlertController, public actionSheetCtrl: ActionSheetController,
    public platform: Platform, public modalCtrl: ModalController, private calendar: Calendar)  {

      this.id = navParams.get('id');
      if (this.id != 0){
      cruzasService.getCruza(this.id)
      .valueChanges().subscribe(cruzas=> {
        
        this.cruzas = cruzas});
  }
  this.datetest= this.cruzas.fechatermino
  


}

dismiss()
  {

    this.viewCtrl.dismiss();
  }

  selectpigeona(){
    let modal = this.modalCtrl.create(Modal4Page);

    modal.onDidDismiss((notes) => {
      console.log(notes);
      this.cruzas.idpaloma1=notes.id;
      this.cruzas.nombrepaloma1=notes.nombre;
      this.cruzas.anillopaloma1=notes.anillo;
        

    });

    
    modal.present();

  }
  selectpigeonb(){
    let modal = this.modalCtrl.create(Modal4Page);

    modal.onDidDismiss((notes) => {
      
      console.log(notes);
      this.cruzas.idpaloma2=notes.id;
      this.cruzas.nombrepaloma2=notes.nombre;
      this.cruzas.anillopaloma2=notes.anillo;
        

    });

    
    modal.present();

  }
  

  poneraviso(){
    
    if(this.cruzas.aviso==true){ 
       
    this.calendar.createEvent(this.eventoCal.title, this.eventoCal.location, this.eventoCal.message, 
      new Date (this.cruzas.fechatermino), new Date (this.cruzas.fechatermino) ).then(
      (msg) => {
        let alert = this.alertCtrl.create({
          title: 'Aviso',
          subTitle: 'Aviso colocado correctamente!',
          buttons: ['Aceptar']
        });
        alert.present();
       
      },
      (err) => {
        this.cruzas.aviso=false;
        let alert = this.alertCtrl.create({
          title: 'Algo Falló',
          subTitle: err,
          buttons: ['Aceptar']
        });
        alert.present();
      }
    );

  };

}

  opcionescruza(){
    if (this.id != 0){
    let actionSheet = this.actionSheetCtrl.create({
      title: 'Opciones',
      cssClass: 'action-sheets-basic-page',
      buttons: [
        
        {
          text: 'Guardar',
          icon: !this.platform.is('ios') ? 'md-add-circle' : null,
          handler: () => {
            console.log('Share clicked');
            
              if (this.id != 0){
                this.cruzasService.editarcruza (this.cruzas);
                let toast = this.toastCtrl.create({
                  message: 'Cruza modificada',
                  duration: 2000,
                  position: 'top'
                });
                toast.present();
               
              }
              else{
          
              this.cruzas.id = Date.now ();
              this.cruzasService.crearcruza (this.cruzas);
              
              let toast = this.toastCtrl.create({
                message: 'Cruza guardada',
                duration: 2000,
                position: 'top'
              });
              toast.present();
               }
              this.viewCtrl.dismiss();
              
            
          }
        },
        {
          text: 'Borrar Cruza',
          role: 'destructive',
          icon: !this.platform.is('ios') ? 'trash' : null,
          handler: () => {
            console.log('Delete clicked');
            let alert = this.alertCtrl.create({
              title: 'Atención',
              subTitle: 'Se borrará la Cruza de manera permanente',
              buttons: [
               
                {
                  text: 'Aceptar',
                  handler: () => {
                    this.show =false
                    this.cruzasService.borrarcruza(this.cruzas);
                    this.viewCtrl.dismiss();
                    let toast = this.toastCtrl.create({
                      message: 'Cruza borrada',
                      duration: 2000,
                      position: 'top'
                    });
                    toast.present();
                    
                  }
                },
                {
                  text: 'Cancelar',
                  handler: () => {
                      this.viewCtrl.dismiss();
                  }
                },
        
        
               ]
            });
            alert.present();
         
          }
        },
        
        {
          text: 'Cancelar',
          role: 'cancel', // will always sort to be on the bottom
          icon: !this.platform.is('ios') ? 'close' : null,
          handler: () => {
            console.log('Cancel clicked');
            this.viewCtrl.dismiss();
          }
        }
      ]
    });
    actionSheet.present();
 }
else {
  let actionSheet = this.actionSheetCtrl.create({
    title: 'Opciones',
    cssClass: 'action-sheets-basic-page',
    buttons: [
      
      {
        text: 'Guardar',
        icon: !this.platform.is('ios') ? 'md-add-circle' : null,
        handler: () => {
          console.log('Share clicked');
            if (this.id != 0){
              this.cruzasService.editarcruza (this.cruzas);
              
              let toast = this.toastCtrl.create({
                message: 'Cruza modificada',
                duration: 2000,
                position: 'top'
              });
              toast.present();
             
            }
            else{
        
            this.cruzas.id = Date.now ();
            this.cruzasService.crearcruza (this.cruzas);
            
            let toast = this.toastCtrl.create({
              message: 'Cruza agregada',
              duration: 2000,
              position: 'top'
            });
            toast.present();
             }
            this.viewCtrl.dismiss();
            
          
        }
      },
      
      
      {
        text: 'Cancelar',
        role: 'cancel', // will always sort to be on the bottom
        icon: !this.platform.is('ios') ? 'close' : null,
        handler: () => {
          console.log('Cancel clicked');
          this.viewCtrl.dismiss();
        }
      }
    ]
  });
  actionSheet.present();



}
}









}



