import { Component } from '@angular/core';
import { NavController, ViewController, Platform, ModalController } from 'ionic-angular';

import { NotesService } from '../../services/notes.service';
import { ModalPage } from '../modal/modal'

import { AngularFireAuth } from 'angularfire2/auth';
import { AngularFireStorageModule, AngularFireStorage, AngularFireUploadTask } from 'angularfire2/storage';
import * as firebase from 'firebase';


@Component({
  selector: 'page-contact',
  templateUrl: 'contact.html'
})
export class ContactPage {

private notes: any = [
    {
   
      
    
  }
 ];
  url: any;
  someTextUrl ='';
id:any=null;

  

  constructor(public navCtrl: NavController, private afAuth: AngularFireAuth, public viewCtrl : ViewController, 
    public modalCtrl: ModalController, private platform:Platform,  public noteService:NotesService) {

      this.noteService.getFavoritas().valueChanges()
      .subscribe(notas=>
            this.notes = notas
         );
       

        this.getSomeText(); 

  }
  getSomeText() {
    
    firebase.storage().ref('pacientes/'+this.id+'/').child('tac').getDownloadURL()
      .then(response => this.someTextUrl = response )
      .catch(error => console.log('error', error)
      
      

    );
    

     

  }


  

}



