import { Component } from '@angular/core';
import { IonicPage,ViewController, NavController, NavParams, Platform, ModalController, AlertController, ToastController } from 'ionic-angular';

import { Observable } from 'rxjs/Observable';
import { NotesService } from '../../services/notes.service';

/**
 * Generated class for the Modal4Page page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-modal4',
  templateUrl: 'modal4.html',
})
export class Modal4Page {

  private notes: any = [
    {
   
      
    
  }
 ];
  url: any;
  

  constructor(public navCtrl: NavController, public navParams: NavParams, public viewCtrl : ViewController, public modalCtrl: ModalController, private platform:Platform,  public noteService:NotesService, public alertCtrl:AlertController, 
   private toast: ToastController) {

    this.noteService.getNotes().valueChanges()
      .subscribe(notas=>{
            this.notes = notas;

        } );

         
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad Modal4Page');
  }

  selectedpigeon(notes){
    console.log(notes);
   
  
  
      this.viewCtrl.dismiss(notes);
    


  }

  dismiss(){
    this.viewCtrl.dismiss();
  }

}



