

import { CruzasService } from '../../services/cruzas.service';

import { Component, ViewChild  } from '@angular/core';
import { NavController, ViewController, Platform, ModalController, AlertController, ToastController } from 'ionic-angular';

import { Modal3Page } from '../modal3/modal3'

import { Observable } from 'rxjs/Observable';

import { AngularFireAuth } from 'angularfire2/auth';

@Component({
  selector: 'page-about',
  templateUrl: 'about.html'
})
export class AboutPage {

  private cruzas: any = [
    {
   
      
    
  }
 ];
  url: any;

  constructor(public navCtrl: NavController, public viewCtrl : ViewController, public modalCtrl: ModalController, private platform:Platform,  public cruzasService:CruzasService, public alertCtrl:AlertController, 
    private afAuth: AngularFireAuth, private toast: ToastController) {

      this.cruzasService.getCruzas().valueChanges()
      .subscribe(cruzas=>{
            this.cruzas = cruzas;

        } );



  }


  verlacruza(id) {

  
    let modal = this.modalCtrl.create(Modal3Page,{id:id} );
    
    
    
        
        modal.present();
        
    
      };
      
    
      nuevacruza(){
    
        let modal = this.modalCtrl.create(Modal3Page,{id:0} );
        modal.present();
      };
    
    
}



